<template>
  <div class='card'>
    <div class='card-content'>
      <h3 class='card-title'> {{station}} - {{city}}, {{state}} </h3>
      <div v-if="loading">
        <h5> Loading... </h5>
      </div>
      <div v-else-if="error">
        Unable to load {{station}} data
        {{ error }}
      </div>
      <div v-else>
        <h6> {{data.Meta.Timestamp}} </h6>
        Cloud Cover
        <ul class='collection'>
          <li v-for="(cloud, index) in data['Cloud-List']" v-bind:key="station + '-cloud-' + index"> {{cloud[0]}} - {{cloud[1]}} </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import config from '../config'
import axios from 'axios'

export default {
  name: 'WeatherStation',
  data: function () {
    return {
      data: [],
      loading: true,
      error: false
    }
  },
  props: ['station', 'city', 'state'],
  computed: {},
  methods: {},
  mounted () {
    axios.get(config.weatherEndpoint + '/' + this.station, {
      'headers': { 'Authorization': 'Bearer ' + config.weatherApiSecret }
    })
      .then(response => {
        this.data = response.data
        console.log(config.weatherEndpoint + '/' + this.station)
        this.loading = false
      })
      .catch(error => {
        console.log(error)
        console.log(config.weatherEndpoint + this.station)
        this.loading = false
        this.error = error.response.data.message
      })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



