<template>
  <div class='row grey lighten-4'>
    <div v-for="(station, index) in stations" :key="'station-' + index" class='col s12 m6 l4'>
      <weather-station :station=station.Station :city=station.City :state=station.State />
    </div>
  </div>
</template>

<script>
import config from '../config'
import axios from 'axios'
import WeatherStation from './WeatherStation'
export default {
  name: 'Weather',
  components: {
    WeatherStation
  },
  data: function () {
    return {
      stations: []
    }
  },
  computed: {},
  methods: {},
  mounted () {
    axios.get(config.weatherEndpoint, {
      'headers': { 'Authorization': 'Bearer ' + config.weatherApiSecret }
    })
      .then(response => {
        this.stations = response.data
      })
      .catch(error => {
        console.log(error)
      })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .row {
    padding: 5%;
  }

  .col {
    //margin-top: 5%;
  }
</style>



